.NavbarItems {
    background: linear-gradient(90deg, rgb(rgb(110,94,254)0%, rgba(73,63,252,1) 100%));
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.1rem;
}

.fa-react {
    margin-left: 0.6rem;
    font-size:1.5rem;
}

.navbar-logo{
    color: blue;
    justify-self: start;
    margin-left: 20px;
    cursor: pointer;
}

.nav-menu{
    display: grid;
    grid-template-columns: repeat(5, auto);
    grid-gap: 10px;
    list-style: none;
    text-align: center;
    width: 70px;
    justify-content: end;
}